import stayCheckform from './businessComponents/stayCheckForm'
import {
	userorderBusishoporder,
	usergiftrecordExchange
} from 'api/api';
let pageData = {
	totalPrice: '',
	count: '',
	currencyUnit: '',

	itemIcon: '',
	itemName: '',
}
let scan = null;
export default {
	data() {
		return {
			pageData: pageData
				/*[{
						          name: '毛绒玩具',
						          price: '5',
						          num:'1',
						          total:'5'
						        }]*/
				,
			searchValue: '',
			tableData: '',

			currentPage: 1,
			pageSizes: [10, 20, 30],
			page: {
				start: 0,
				length: 10,
				draw: 1,
			},
			dialogVisible: false,
			dialogVisibleCode: false,
			dialogTyle: '',
			tkValue:'',
			showTips:false,
			code:'',
			showCode:true

		}
	},
	components: {
		stayCheckform
	},
	created() {
		if(!window.plus){
				this.showCode=false
			}
		this.init()
	},
	watch: {
		tkValue(t){
			if(t!=''){
				this.showTips=false
			}
		},
		searchValue(value) {
			if(!value) {
				this.init()
			}
		},
		dialogVisibleCode(f){
			if(!f&&scan!=null){
				scan.close()
				scan=null
			}
		}
	},
	methods: {
		checkQrcode(){
			if(this.tkValue==''||!/^[0-9]{1,2}?$/.test(this.tkValue)){
				return this.showTips=true
			}
			if(scan){
				scan.start()
			}else {
				document.addEventListener('plusready',this.getkScan())
				
			}
		},
		getkScan(){
			 
				scan = new plus.barcode.Barcode('scid',{},{background:'#000000'});
					scan.onmarked = ( type,result,file)=>{
										var text = '未知: ';
										switch(type){
											case plus.barcode.QR:
											text = 'QR: ';
											break;
											case plus.barcode.EAN13:
											text = 'EAN13: ';
											break;
											case plus.barcode.EAN8:
											text = 'EAN8: ';
											break;
										}
										this.getkCode(result)
									}
					 
					scan.start();

		},
		getkCode(code){
			scan.close();
			scan=null
			this.code=code
			usergiftrecordExchange({shopUserId:this.$store.state.user.shopUserId,quantity:this.tkValue,orderId:code}).then(res => {
					this.init()
					 
				}).catch(err=>{
					 
				})
		},
		verification(){
			if(!window.plus){
				this.$notify.warning({
		          title: this.$t('commom.alertTitle'),
		          message:  this.$t('commom.phoneType'),
		          duration:3000
		        });
			}else{
				this.dialogVisibleCode=true
			}
		},
		init() {
			//busItemName
			userorderBusishoporder(this.page).then(res => {
				this.tableData = { ...res.data.obj,
					...res.data.obj.busiItem
				}
			})
		},
		onQuery() {
			if(this.searchValue) {
				userorderBusishoporder({ ...this.page,
					busItemName: this.searchValue
				}).then(res => {
					this.tableData = { ...res.data.obj,
						...res.data.obj.busiItem
					}
				})
			}
		},
		showDilog(type, data = typeFormData) {
			this.pageData = {
				 ...data,
					...data.busiItem
			}
			this.dialogVisible = true
			this.dialogTyle = type
		},
		getpageData() {
			userorderBusishoporder({ ...this.page,
				busItemName: this.searchValue
			}).then(res => {
				this.tableData = { ...res.data.obj,
					...res.data.obj.busiItem
				}
			})
		},
		handleSizeChange(val) {
			this.page.length = val
			this.page.start = (this.currentPage - 1) * this.page.length
			this.getpageData()
		},
		handleCurrentChange(val) {
			this.currentPage = val;
			this.page.start = (this.currentPage - 1) * this.page.length
			setTimeout(() => {
				this.getpageData()
			}, 0)
		},
	}
}



